.pagination {
  $self: &;
  margin: 2rem 0 0;

  &__count {
    display: block;
    text-align: center;

    &::before {
      content: "";
      display: block;
      width: 2.5em;
      height: 4px;
      margin: 0 auto 1rem;
      background-color: $color__primary-500;
    }
  }

  &__ctrl {
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    justify-content: center;
  }

  &__nav {
    @include basic-button();
    flex: 0 1;

    &:visited {
      color: #fff;
    }

    &:hover,
    &:active,
    &:focus {
      color: $color__primary-500;
    }

    &:hover > #{ $self }__nav-icon #{ $self }__nav-icon-path,
    &:focus > #{ $self }__nav-icon #{ $self }__nav-icon-path {
      fill: $color__primary-500;
    }

    &:hover > #{ $self }__nav-icon #{ $self }__nav-icon-path,
    &:focus > #{ $self }__nav-icon #{ $self }__nav-icon-path {
      fill: $color__primary-500;
    }
  }

  &__nav + #{ $self }__nav {
    margin-left: 0.25rem;

    @include mappy-bp(medium) {
      margin-left: 0.5rem;
    }
  }

  &__nav--number {
    display: none;

    @include mappy-bp(small) {
      display: inline-block;
    }
  }

  &__nav--older {
    padding-left: 0.75rem;
  }

  &__nav--newer {
    padding-right: 0.75rem;
  }

  &__nav--active {
    border-color: $color__primary-800;
    background-color: $color__grey-800;

    &:hover,
    &:active,
    &:focus {
      border-color: $color__primary-800;
      background-color: $color__grey-800;
      color: #fff;
    }
  }

  &__nav--disabled {
    color: $color__grey-600;
    pointer-events: none;
    cursor: auto;
    background-color: $color__grey-200;
    border-color: $color__grey-200;

    &:visited {
      color: $color__grey-600;
    }
  }

  &__nav-icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    vertical-align: bottom;
  }

  &__nav-icon-path {
    fill: #fff;
    transition: fill 0.2s;
  }

  &__nav-icon-path--disabled {
    fill: $color__grey-600;
  }
}
